.mainContainer {
    display: flex;
    background-color: white;
    color: white;
    cursor: default;
    font-family: Arial, Arial, Helvetica, sans-serif;
    text-align: center;
    font-size: 25px;
}

.infoContainer {
    display: column;
    background-color: #003e29; 
    padding: 20px;
    border-radius: 5px;
    border-right: 2px solid black;
    border-bottom: 4px solid black;
    margin-top: 4%;
    margin-left: 1%;
}

.title {
    font-size: 45px;
}

img {
    border: 3px solid black;
    border-radius: 15px;
    width: 300px;
    height: 400px;
}

.generalContainer {  
    display: column;
    background-color: #0b3124; 
    padding: 20px;
    border-radius: 15px;
    border-right: 3px solid black;
    border-bottom: 6px solid black;
    margin-top: 4%;
    margin-left: 1%;
    margin-right: 1%;
    text-align: start;
    margin-top: 16px;
}
.workButton {
    cursor: pointer;
    width: 300px;
    background-color: rgb(4, 37, 65);
    color : white;
    border-radius: 8px;
}

.workButton:hover {
    background-color: rgb(17, 65, 104);
    width: 320px;
}

.workContainer {
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    padding: 15px;
    background-color: #0e3a2b; 
}

.skillsContainer {
    display: flex;
    justify-content: space-around;
}

.skillsItems {
    display: column;
}

a {
    color: white
}

.iconStyle {
    width: 50px;
    height: 50px;
}

